.cursor-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  pointer-events: none;
  z-index: 9999;
}

.cursor-dot {
  position: absolute;
  width: 8px;
  /* Increased size */
  height: 8px;
  /* Increased size */
  background-color: red;
  border-radius: 50%;
  transform: translate(-50%, -50%);
  pointer-events: none;
  transition: transform 0.2s ease-out;
}

.cursor-image {
  position: absolute;
  width: 60px;
  /* Adjust size as needed */
  height: 60px;
  /* Adjust size as needed */
  transform: translate(-50%, -50%);
}

.cursor-dot {
  position: absolute;
  width: 8px;
  height: 8px;
  background-color: white;
  transition: box-shadow 0.3s ease-in-out;
  border-radius: 50%;
  transform: translate(-61%, -57%);
  pointer-events: none;
  transition: box-shadow 0.5s ease-in-out;
}

.cursor-dot.hover,
.cursor-circle.hover {
  transform: translate(-50%, -50%) scale(1.5);
}

.cursor-dot.click {
  animation: clickAnimationDot 0.3s ease-out;
}

.cursor-circle.click {
  animation: clickAnimationCircle 0.3s ease-out;
}

@keyframes clickAnimationDot {
  0% {
    transform: translate(-50%, -50%) scale(1);
  }

  50% {
    transform: translate(-50%, -50%) scale(0.75);
  }

  100% {
    transform: translate(-50%, -50%) scale(1);
  }
}

@keyframes clickAnimationCircle {
  0% {
    transform: translate(-50%, -50%) scale(1);
    opacity: 1;
    border-color: white;
  }

  50% {
    transform: translate(-50%, -50%) scale(1.5);
    opacity: 0.5;
    border-color: red;
  }

  100% {
    transform: translate(-50%, -50%) scale(1);
    opacity: 1;
    border-color: white;
  }
}

