.roadmap {
  #slideshow {
    margin: 0 auto;
    padding-top: 50px;
    height: 600px !important;
    width: 100%;
    box-sizing: border-box;
    overflow: hidden;

    .slideshow-title {
      font-family: "Allerta Stencil";
      font-size: 62px;
      color: #fff;
      margin: 0 auto;
      text-align: center;
      margin-top: 25%;
      letter-spacing: 3px;
      font-weight: 300;
    }

    .sub-heading {
      padding-top: 50px;
      font-size: 18px;
    }

    .sub-heading-two {
      font-size: 15px;
    }

    .sub-heading-three {
      font-size: 13px;
    }

    .sub-heading-four {
      font-size: 11px;
    }

    .sub-heading-five {
      font-size: 9px;
    }

    .sub-heading-six {
      font-size: 7px;
    }

    .sub-heading-seven {
      font-size: 5px;
    }

    .entire-content {
      margin: auto;
      width: 190px;
      perspective: 1000px;
      position: relative;
      padding-top: 80px;
    }

    .content-carrousel {
      width: 100%;
      position: absolute;
      float: right;
      animation: roadmaprotar 20s infinite linear;
      transform-style: preserve-3d;
    }

    .content-carrousel:hover {
      animation-play-state: paused;
      cursor: pointer;
    }

    .content-carrousel figure {
      width: 200px;
      height: 300px;
      position: absolute;
    }

    .content-carrousel figure:nth-child(1) {
      transform: rotateY(0deg) translateZ(240px);
    }

    .content-carrousel figure:nth-child(2) {
      transform: rotateY(51.43deg) translateZ(240px);
    }

    .content-carrousel figure:nth-child(3) {
      transform: rotateY(102.86deg) translateZ(240px);
    }

    .content-carrousel figure:nth-child(4) {
      transform: rotateY(154.29deg) translateZ(240px);
    }

    .content-carrousel figure:nth-child(5) {
      transform: rotateY(205.72deg) translateZ(240px);
    }

    .content-carrousel figure:nth-child(6) {
      transform: rotateY(257.15deg) translateZ(240px);
    }

    .content-carrousel figure:nth-child(7) {
      transform: rotateY(308.58deg) translateZ(240px);
    }
  }

  .shadow {
    position: absolute;
    box-shadow: 0px 0px 20px 0px #000;
    border-radius: 1px;
  }

  .content-carrousel img {
    image-rendering: auto;
    transition: all 300ms;
    width: 100%;
    height: 100%;
    object-fit: contain;
  }

  .content-carrousel img:hover {
    transform: scale(1.2);
    transition: all 300ms;
  }
}

@keyframes roadmaprotar {
  from {
    transform: rotateY(360deg);
  }

  to {
    transform: rotateY(0deg);
  }
}

