.center ul li a {
  text-decoration: none;
  color: #fff;
  font-size: 14px;
  position: relative;
  transition: color 0.3s, transform 0.3s;
}

.center ul li a::before,
.center ul li a::after {
  content: "";
  position: absolute;
  width: 0;
  height: 4px;
  background: #ff3333;
  transition: ease-in 0.3s;
  box-shadow: 0 0 10px rgba(255, 0, 0, 0.8), 0 0 20px rgba(255, 0, 0, 0.5),
    0 0 30px rgba(255, 0, 0, 0.2);
}

.center ul li a::before {
  left: 0;
  bottom: -2px;
}

.center ul li a::after {
  right: 0;
  top: -5px;
}

.center ul li a:hover {
  color: #ff3333;
  transform: scale(1.1);
}

.center ul li a:hover::before,
.center ul li a:hover::after {
  width: 100%;
}

