.presale-full-sec {
  .table-box {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 20px;
    max-width: 610px;
    margin: auto;

    table {
      border-collapse: separate;
      border-spacing: 0;
      width: 120%;
      max-width: 100%;
    }

    th,
    td {
      padding: 0px;
      text-align: center;
      position: relative;
      /* background-color: black; */
      color: white;
      animation: glow 4s infinite alternate;
    }

    td {
      img {
        width: 15px;
      }
    }

    /* Enhanced neon red glowing border animation */

    /* Apply glowing border to rows 2-5 only (excluding first row) */
    tr:nth-child(n + 2):nth-child(-n + 5) td {
      border-bottom: 1px solid rgba(255, 0, 0, 0.3);
      /* Thinner and transparent red border */
    }

    /* Apply glowing border only to columns 2-10 */
    th:nth-child(n + 2):nth-child(-n + 11),
    td:nth-child(n + 2):nth-child(-n + 11) {
      border-left: 1px solid rgba(255, 0, 0, 0.3);
      /* Thinner and transparent red border */
      border-right: 1px solid rgba(255, 0, 0, 0.3);
      /* Thinner and transparent red border */
    }

    /* Remove borders from the first row, first column, and last row */
    th:first-child,
    td:first-child {
      width: 200px;
      border: none;
    }

    tr:first-child th,
    tr:first-child td {
      border-bottom: none;
      /* Remove bottom border for the first row */
    }

    tr:last-child td {
      border: none;
      /* Ensure no border for cells in the last row */
    }

    /* Add red border specifically under the 'Presale' row */
    tr:nth-child(2) td {
      border-bottom: 1px solid rgba(255, 0, 0, 0.3);
      /* Add border under 'Presale' row */
    }

    /* Apply Impact font to the first row and first column */
    tr:first-child th,
    td:first-child {
      font-family: "Impact", sans-serif;
    }

    /* Add a 20px gap between the first and second row */
    tr:first-child {
      border-bottom: 20px solid black;
      /* Create space below the first row */
    }
  }
}

@keyframes glow {
  from {
    box-shadow: 0 0 8px rgba(255, 0, 0, 0.2);
    /* More visible dim glow */
  }

  to {
    background-color: black;
    box-shadow: 0 0 25px rgba(255, 0, 0, 0.6);
    /* Brighter and more intense glow */
  }
}

