/* --- --- --- Imports fonts being used --- --- ---*/

@font-face {
  font-family: Minecraft;
  src: url(./MinecraftBold.otf);
  font-weight: 700;
}

@font-face {
  font-family: termin-font;
  src: url(./terminator.ttf);
  font-weight: 700;
}

/* Dashboard.css */

.dashboard {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

/* --- --- --- Controls top horizontal Nav bar text --- --- ---*/
.dashboard-header {
  font-family: 'Termin-font';
  font-size: 40px;
}

/* --- --- --- Controls background video styling --- --- ---*/
.dashboard-video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.dashboard-content {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  max-width: 60%;
  margin: 0 auto;
  z-index: 1;
}

/* --- --- --- Controls text in box 1 --- --- ---*/

.flex-1 {
  font-family: 'termin-font';
}

.text-orange {
  font-family: 'Minecraft-Reg';
  font-size: large;
}

/* --- --- --- creates transparent text used for styling transitions --- --- ---*/

.transparent-text {
  color: rgba(255, 255, 255, 0.0);
}

/* --- --- --- Controls bottom dashboard box --- --- ---*/

.dashboard-box {
  /*background: linear-gradient(to bottom, rgb(146, 11, 7), rgb(0, 0, 0)); */
  background: radial-gradient(circle, rgb(78, 2, 2), rgb(0, 0, 0));
  transform: none;
  transition: transform 0.3s ease;
  border-radius: 10px;
  padding: 20px;
  width: 100%;
  position: relative;
  z-index: 2;
  border: 2px solid black;
  /* Example border */
  opacity: 1;
  margin-bottom: 25px;
  position: relative;
  box-shadow: 0 0 6px 3px rgba(252, 1, 1, 0.5);
  /* Glow effect */
}

/* --- --- --- Controls top dashboard box --- --- ---*/

.new-dashboard-box {
  /*background: radial-gradient(circle, rgb(78, 2, 2), rgb(0, 0, 0)); */
  background-color: rgba(0, 0, 0, 0.89);
  border-radius: 10px;
  padding: 20px;
  /* width: 100vw;
  height: 5vw; */
