  /* White border */
  border-radius: 50%;
  /* Round border for circular shape */
  width: 40px;
  /* Adjust width as needed */
  height: 40px;
  /* Adjust height as needed */
  background-color: transparent;
  /* Transparent background */
  margin-left: 50%;

}

.tb-switch-vertical svg {
  fill: #ffffff;
  /* Change image color to white */
  width: 70%;
  /* Adjust icon size */
  height: auto;

}

